<template>
    <div class="culture">
        <div class="header">
            <span @click="back" class="back">
                <img src="../assets/img/back.png">
            </span>
            <span class="usersTitle">{{ title }}</span>
            <span class="back"></span>
        </div>
        <ul class="articles">
            <span v-for="(article, index) in articles" :key="index" @click="getArticleDetails(index)">
            <li class="article1" v-if="article.fArticleCoverList.length === 1 || article.fArticleCoverList.length === 2">
                <span class="cover" v-for="(cover, index) in article.fArticleCoverList" :key="index" v-show="index < 1">
                    <img :src="cover.fArticleCover">
                </span>
                <span class="articleInfo">
                    <span class="title">{{ article.fArticleTitle }}</span>
                    <span class="detail">
                        <span class="author">{{ article.fAuthor }}</span>
                        <span class="views">
                            <span class="viewIcon">
                                <img src="../assets/img/view.png">
                            </span>
                            <span>{{ article.fReadCnt }}</span>
                        </span>
                    </span>
                </span>
            </li>
            <li class="article2" v-if="article.fArticleCoverList.length === 0 || article.fArticleCoverList.length > 2">
                <span class="title">{{ article.fArticleTitle }}</span>
                <ul class="covers">
                    <li v-for="(cover, index) in article.fArticleCoverList" :key="index" v-show="index < 3">
                        <img :src="cover.fArticleCover">
                    </li>
                </ul>
                <span class="detail">
                    <span class="author">{{ article.fAuthor }}</span>
                    <span class="views">
                        <span class="viewIcon">
                            <img src="../assets/img/view.png">
                        </span>
                        <span>{{ article.fReadCnt }}</span>
                    </span>
                </span>
            </li>
            </span>
        </ul>
    </div>
</template>

<script>
import store from '../vuex/store'
import { getHotNews } from '../api/getdata'
export default {
  name: 'culture',
  data () {
    return {
      articles: [],
      title: ''
    }
  },
  mounted () {
    this.getArticles()
    this.title = store.state.articleType
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getArticles () {
      let typeId
      if (store.state.articleType === '文化') {
        typeId = 3
      } else if (store.state.articleType === '养生') {
        typeId = 4
      }
      let data = getHotNews({typeId: typeId, pageIndex: 1, pageSize: 300})
      data.then(res => {
        console.log(res.data.result)
        this.articles = res.data.result
      })
    },
    getArticleDetails (index) {
      store.commit('saveArticleId', this.articles[index].fArticleId)
      this.$router.push('/home/index/news')
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    font-size: 0.48rem;
    background: #fff;
    z-index: 99;
    top: 0;
    left: 0;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .articles {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 1.5rem;
    list-style: none;
  }

  .article1,
  .article2 {
    padding: 0.37333rem 0;
    border-bottom: 1px solid #f5f5f5;
  }

  .article1 {
    display: flex;
  }

  .cover {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.15rem;
    overflow: hidden;
    width: 4rem;
  }

  .title {
    display: inline-block;
    width: 100%;
    text-align: start;
    color: #333333;
  }

  .articleInfo {
    flex: 1 1 auto;
    display: flex;
    padding: 0.21333rem 0.37333rem;
    flex-direction: column;
    justify-content: space-between;
  }

  .detail {
    display: flex;
    justify-content: space-between;
    color: #666666;
  }

  .detail span {
    font-size: 0.32667rem;
  }

  .viewIcon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 0.4rem;
    height: 0.3rem;
    margin-right: 0.10667rem;
  }

  .views {
    display: flex;
    align-items: center;
  }

  .covers {
    display: flex;
    list-style: none;
    flex-wrap: wrap;
    padding: 0.32rem 0;
    justify-content: space-between;
  }

  .covers li {
    width: 32%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.1rem;
    overflow: hidden;
  }
</style>



